 "use client";
 import Image from "next/image";
import { useState } from "react";
import Beaupreneur from "./beaupreneur";
import Iwaria from "./iwaria";

const logoContent = {
    starks: <div className=""></div>,
    executivePros: <div className=""></div>,
    stacai: <div className=""></div>,
    iwaria: <div className=""><Iwaria /></div>,
    beaupreneur: <div className=""><Beaupreneur /> </div>
};

type LogoKey = keyof typeof logoContent;
export default function Feedback () {

    const [selectedLogo, setSelectedLogo] = useState<LogoKey | null>('starks');
    
    const handleClick = (logoName: LogoKey) => {
        setSelectedLogo(selectedLogo === logoName ? null : logoName);
    }
   return ( 
   <div className="flex flex-col items-center lg:px-28 lg:py-10 mt-16">
    <h1 className="text-[44px] font-normal text-center text-white w-[80%]">Discover the <span className="text-[#60a6e7]">transformative  stories</span> of startups that scaled new heights with us</h1>
    <div className="flex flex-row items-center justify-center border border-1 rounded-r-full rounded-l-full border-[#60a6e7]  mt-10 w-full px-16 h-14">
        <div className={`flex flex-row gap-1 items-center  object-contain w-full 
        ${selectedLogo === 'starks'? 'via-[rgba(12,38,69,0.8)] bg-customBlue1' : ""} `} 
        onClick={() => handleClick("starks")}>
            <Image src="/images/starks (1).svg" alt="feedback" width={100} height={100} className="w-10 h-10" />
            <span className="text-white font-normal text-[15px]">Starks</span>
        </div>
        <div className={`curso-pointer w-full  ${selectedLogo === 'executivePros'? 'via-[rgba(12,38,69,0.8)] bg-customBlue1 h-' : ""} `} onClick={() => handleClick("executivePros")}>
        <Image src="/images/ExecutivePros Logo.svg" alt="arrow" width={100} height={100} className="w-24 h-24 object-contain" />
        </div>
        <div className={`curso-pointer w-full ${selectedLogo === 'stacai'? 'via-[rgba(12,38,69,0.8)] bg-customBlue1' : ""} `} onClick={() => handleClick("stacai")}>
        <Image src="/images/stacai.svg" alt="arrow" width={100} height={100} className="w-24 h-24 object-contain" />
        </div>
        <div className={`curso-pointer w-full ${selectedLogo === 'iwaria'? 'via-[rgba(12,38,69,0.8)] bg-customBlue1' : ""} `} onClick={() => handleClick("iwaria")}>
        <Image src="/images/iwaria.svg" alt="arrow" width={100} height={100} className="w-24 h-24 object-contain" />
        </div>
        <div className={`curso-pointer w-full ${selectedLogo === 'beaupreneur'? 'via-[rgba(12,38,69,0.8)] bg-customBlue1' : ""} `} onClick={() => handleClick("beaupreneur")}>
        <Image src="/images/Beaupreneur.svg" alt="arrow" width={100} height={100} className="w-32 h-32 object-contain" />
        </div>
    </div>

    {selectedLogo && (
        <div className="mt-4 p-4">
          {logoContent[selectedLogo]}
        </div>
      )}
   </div>
)}